 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-create-block-testimonial {
	position: relative;

	.cwp-testimonial {
		position: relative;

		&-overlay {
			position: absolute;
			inset: 0
		}

		&-title,
		&-desc {
			position: relative
		}
	}

	.wp-block-button {
		position: relative
	}

	.wp-block-button__link {
		font-size: inherit
	}
}
